import React from "react";
import "./Benefits.scss";
import Benefit from "../Benefit/Benefit";
import starImg from "../../media/images/benefits/star.png";
import winnerImg from "../../media/images/benefits/winner.png";
import certifImg from "../../media/images/benefits/certification.png";
import giftImg from "../../media/images/benefits/gift.png";
import emailImg from "../../media/images/benefits/email.png";
import publishImg from "../../media/images/benefits/publish.png";

const benefitsState = [
  {
    id: 1,
    text: "  1         ",
    img: starImg,
    highlighted: "",
  },
  {
    id: 2,
    text: "  +     ;)",
    img: winnerImg,
    highlighted: "",
  },
  {
    id: 3,
    img: certifImg,
    text: "      ",
    highlighted: "",
  },
  {
    id: 4,
    text: "       ",
    img: giftImg,
    highlighted: "",
  },
  {
    id: 5,
    text: "   email   1   ",
    img: emailImg,
    highlighted: "",
  },
  {
    id: 6,
    text: "    .",
    img: publishImg,
    highlighted: "!",
  },
];

const Benefits: React.FC = () => {
  return (
    <section className="benefits">
      <h2 className="benefits__title title">   </h2>
      <div className="benefits__row">
        {benefitsState.map((b) => (
          <Benefit
            key={b.id}
            img={b.img}
            text={b.text}
            highlighted={b.highlighted}
          />
        ))}
      </div>
    </section>
  );
};

export default Benefits;
